function sortByDate(a, b) {
  return Date.parse(b.frontmatter.date) - Date.parse(a.frontmatter.date)
}

export function filterPostList(baseList, filter, sort = sortByDate) {
  const filteredList = baseList.filter(filter)

  return filteredList.sort(sort)
}

export function getPostUrl(node) {
  return `/blog/${node.parent.relativeDirectory}/${node.parent.name}`
}
