import React from 'react'
import { graphql, navigate, Link } from 'gatsby'
import PropTypes from 'prop-types'

import { withStyles } from '@material-ui/styles'

import Typography from '@material-ui/core/Typography'

import SEO from '../components/seo'
import LinkButton from '../components/link-button'
import HeadImage from '../components/images/HeadImage'

import { StyledLayout } from '../components/layout/new-layout'

import svgPath from '../images/record.svg'

import { getPostUrl } from '../utils'

import '../base-queries'

const styles = theme => ({
  banner: {
    color: theme.palette.primary.contrastText,
    paddingBottom: 80,
    paddingTop: 40,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'column-reverse',
      alignItems: 'center',
    },
  },
  bodyTitle: {
    padding: '20px 20px',
  },
  title: {
    color: theme.palette.primary.contrastText,
    paddingTop: '20px',
    [theme.breakpoints.down('sm')]: {
      textAlign: 'center',
    },
  },
  subTitle: {
    color: theme.palette.primary.contrastText,
    [theme.breakpoints.down('sm')]: {
      textAlign: 'center',
    },
  },
  gif: {
    background: '#282c34',
    padding: '20px 20px',
    paddingBottom: 0,
    borderRadius: '3px 3px 0 0',
    overflow: 'hidden',
  },
  allButton: {
    textAlign: 'center',
    paddingTop: 20,
  },
  headImage: {
    width: 200,
    borderRadius: 100,
    marginLeft: 40,
    [theme.breakpoints.down('sm')]: {
      marginLeft: 0,
    },
  },
})

const postItemStyles = {
  root: {
    margin: '0 20px',
    '& + &': {
      marginTop: 20,
      paddingTop: 20,
      borderTop: '1px solid #aaa',
    },
  },
  caption: {
    marginBottom: 15,
  },
  body: {
    marginBottom: 20,
  },
}

function PostItem({ post, classes: { root, caption, body } }) {
  const path = getPostUrl(post)

  function goToPost() {
    navigate(path)
  }

  return (
    <div className={root}>
      <Typography variant="h6" onClick={goToPost}>
        <Link to={path}>{post.frontmatter.title}</Link>
      </Typography>
      <Typography variant="caption" className={caption}>
        {post.frontmatter.date}
      </Typography>
      <Typography variant="body1" className={body}>
        {post.excerpt}
      </Typography>
      <LinkButton variant="contained" color="secondary" to={path}>
        read
      </LinkButton>
    </div>
  )
}

const StyledPostItem = withStyles(postItemStyles)(PostItem)

const IndexPage = ({ classes, data }) => {
  // return <p>Index</p>

  const posts = data.allMdx.nodes

  const banner = (
    <div className={classes.banner}>
      <div>
        <Typography variant="h3" className={classes.title} gutterBottom>
          前端，如此有趣
        </Typography>
        <Typography variant="subtitle1" className={classes.subTitle}>
          我是Jokcy，我遨游在前端的海洋并把精彩展示给你
        </Typography>
      </div>
      <HeadImage className={classes.headImage} />
    </div>
  )

  return (
    <StyledLayout banner={banner}>
      <SEO title="首页" keywords={[`Jokcy`, '博客', '前端', 'React']} />
      <div className={classes.gif}>
        <img src={svgPath} alt="" width="600px" style={{ marginLeft: 40 }} />
      </div>
      <Typography variant="h4" className={classes.bodyTitle}>
        Latest Blogs
      </Typography>
      {posts.map(post => (
        <StyledPostItem key={post.id} post={post} />
      ))}
      <div className={classes.allButton}>
        <LinkButton color="primary" to="/tags/__all">
          全部博客
        </LinkButton>
      </div>
    </StyledLayout>
  )
}

IndexPage.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(IndexPage)

export const pageQuery = graphql`
  query {
    allMdx(sort: { fields: frontmatter___date, order: DESC }, limit: 5) {
      nodes {
        ...CommonMdxQuery
      }
    }
  }
`
